.app {
  margin: 0;
  padding: 0px;
  box-sizing: border-box;
}

/* This is Navbar pages styles */
nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 10vh;
  width: 100%;
  background-color: #0003;
  position: relative;
  font-family: "Lora", serif;
  background-image: linear-gradient(to right, #868f96 0%, #596164 100%);
}
.nav-link {
  width: 80%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.color {
  list-style: none;
  color: black;
}

.color:hover {
  color: aqua;
  text-decoration: none;
}

/* This is Img pages style only  for main-pages background */
.showcase {
  width: 100%;
  height: 55vh;
  padding-left: -5%;
}

/* This is the Second pages components */
.background-words {
  color: white;
}

.background {
  font-weight: bold;
  position: absolute;
  margin-top: -40%;
  margin-left: 30%;
  font-size: 20px;
}
.background-1 {
  font-weight: bold;
  position: absolute;
  font-size: 20px;
  margin-top: -35%;
  margin-left: 30%;
}
.home-welcome-background {
  font-size: 18px;
  font-weight: bold;
  margin-left: 10%;
  font-family: "Odibee Sans", cursive;
}
.home-welcome-background-1 {
  font-size: 18px;
  font-weight: bold;
  margin-left: 27%;
  margin-top: 10px;
  font-family: "Odibee Sans", cursive;
}
.flex-container {
  margin-left: 10%;
}
.flex-container > div {
  font-size: 15px;
}
.Home-page-below-pic {
  padding-top: 8%;
  width: 100%;
  height: 45vh;
}
.Home-pages-below-paragraphy {
  font-size: 24px;
  font-weight: bold;
  margin-left: 10%;
  color: blue;
}
.Home-pages-below-paragraphy-1 {
  font-size: 20px;
  margin-left: 10%;
  margin-right: 5%;
}

/* Footer */
.footersection {
  background-color: rgb(27, 27, 27);
  color: white;
  text-align: center;
  padding: 3px;
  width: 100%;
}

.footer-size {
  font-size: 15px;
  width: 100%;
}

/* This is About page topic Components style only */
.About-topic {
  padding: 40px;
  width: 100%;
  background-image: url("./Pages/about-abst.jpg");
  background-repeat: no-repeat;
  height: 500px;
  background-size: cover;
  font-family: "Roboto", sans-serif;
}
.about-topic-style-1 {
  font-size: 35px;
  margin-left: 30%;
  font-weight: bold;
}
.about-topic-style {
  padding-right: 2%;
  margin-left: 0%;
  margin-right: 1%;
  font-size: 28px;
  font-family: "Roboto", sans-serif;
}

/* Below page style only About-below */
.About-below {
  font-family: "Roboto", sans-serif;
}
.below-header-tag {
  font-weight: bold;
  text-align: center;
  margin-top: 10%;
  margin-bottom: 5%;
  font-size: 35px;
}
.paragraph-tag {
  font-size: 27px;
  margin-left: 10%;
  margin-right: 2%;
}
.block-contain {
  display: block;
  unicode-bidi: isolate;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
  margin-inline-start: auto;
  margin-inline-end: auto;
}

.below-page-middle-paragraph {
  font-family: sans-serif;
  font-style: italic;
  margin-top: 10%;
  margin-left: 10%;
  font-size: 27px;
}

.below-page-verse {
  text-align: end;
  font-size: 28px;
  margin-right: 12%;
  font-style: italic;
}
.block-contain {
  display: block;
  unicode-bidi: isolate;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
  margin-inline-start: auto;
  margin-inline-end: auto;
}

.below-page-background-color {
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
.below-text {
  text-align: center;
  font-size: 35px;
  color: rgb(16, 38, 243);
}
.below-address-main {
  font-size: 37px;
  text-align: center;
  color: rgb(16, 38, 243);
}

.below-address {
  font-size: 28px;
  text-align: center;
  margin-top: -10px;
}

.below-page-pastors-text {
  font-size: 37px;
  margin-left: 10%;
}

.pastor-bio {
  width: 100%;
  height: 40vh;
}

.pastor-bio-text {
  margin-top: 2%;
  margin-left: 10%;
  font-size: 23px;
}
.pastor-bio-text-1 {
  font-size: 23px;
  margin-top: 2%;
  margin-left: 10%;
  margin-right: 2%;
}

/* This is the  Project style only */

.Project-header-pic {
  width: 100%;
  height: 50vh;
}
.Project-icon {
  position: absolute;
  margin-top: -250px;
  margin-left: 60%;
  color: cyan;
  font-size: 24px;
}
.Project-icon-2 {
  position: absolute;
  margin-top: -220px;
  margin-left: 50%;
  color: cyan;
  font-size: 24px;
}
.Project-page-main-header-title {
  padding-top: 40px;
  text-align: center;
  font-style: italic;
  font-size: 30px;
  color: rgb(83, 83, 243);
}

.Project-page-volunteer {
  background: rgb(206, 203, 203);
}

.volunteer-page-paragraph {
  margin: 10px;
  font-family: "Abel";
  font-size: 25px;
}
.project-middle {
  text-align: center;
}
.project-middle-header {
  color: rgb(83, 83, 243);
  font-size: 45px;
  font-style: italic;
}
.project-page-video {
  width: 100%;
  height: 60vh;
}
.project-end-paragraph {
  font-size: 22px;
  font-family: "Abel";
}

/* This is a gallery image style only */
.pic-gallery {
  margin-left: 4px;
  margin-right: 0px;
}

.pic-gallery img {
  margin-top: 10px;
  vertical-align: middle;
}

.gallery-page-paragraph {
  color: green;
  text-align: center;
  background-color: black;
}

/* This screen is for bigger phone  */
@media only screen and (min-width: 600px) {
  nav {
    width: 100%;
  }
  .showcase {
    width: 100%;
    height: 70vh;
  }
  .background {
    font-size: 24px;
    margin-top: -34%;
    margin-left: 30%;
  }
  .background-1 {
    font-size: 24px;
    margin-top: -30%;
    margin-left: 30%;
  }
  .Home-page-below-pic {
    height: 47vh;
  }

  .footersection {
    width: 100%;
  }

  /* This is About-page Topic style */
  .about-topic-style {
    margin-left: 5%;
  }

  /* This is About-page Below style */
  .paragraph-tag {
    margin-right: 5%;
  }
  .below-page-middle-paragraph {
    margin-right: 5%;
  }
  .pastor-bio-text {
    margin-right: 5%;
  }
  .pastor-bio-text-1 {
    margin-right: 5%;
  }
}
/*This is screen for ipad and table */
@media only screen and (min-width: 768px) {
  /* Pages for Navbar */
  nav {
    width: 100%;
  }
  .showcase {
    width: 100%;
    height: 70vh;
  }
  .background {
    margin-top: -20%;
    margin-left: 30%;
  }
  .background-1 {
    margin-top: -17%;
    margin-left: 30%;
  }
  .home-welcome-background {
    font-size: 25px;
    font-style: italic;
  }
  .home-welcome-background-1 {
    font-size: 25px;
    margin-left: 35%;
  }
  .flex-container {
    margin-left: 25%;
  }
  .flex-container > div {
    font-size: 20px;
  }
  .Home-page-below-pic {
    height: 55vh;
    padding-top: 2px;
    margin-top: 20px;
  }

  /* Footer-page */
  .footersection {
    width: 100%;
  }

  /* This is About-page Topic style */
  .About-topic {
    height: 36vh;
    opacity: 0.9;
  }
  .about-topic-style {
    margin-top: 5%;
    color: white;
  }
  .about-topic-style-1 {
    margin-left: 40%;
    color: white;
  }

  /* This is the  Project style only */
  .Project-page-volunteer-pic {
    height: 50vh;
  }
  /* This is Gallery style page */
  .imgall {
    display: flex;
    flex-wrap: wrap;
    padding: 0 20px;
  }

  .pic-gallery {
    flex: 47%;
    max-width: 47%;
